@import url(https://fonts.googleapis.com/css2?family=Khula:wght@300;400;600;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  font-family: 'Khula', sans-serif;
}

h2 {
  font-family: 'Khula', sans-serif;
}

h3 {
  font-family: 'Khula', sans-serif;
}

P {
  font-family: 'Khula', sans-serif;
}

a {
  font-family: 'Khula', sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.nav-item {
  color: rgb(72, 72, 72)!important;
}

.bg-secondary {
  background-color: #efefef!important;
}

.bg-secondary-grad {
  background: linear-gradient(90deg, rgba(255,255,255,1) 0%, rgba(239,239,239,1) 50%, rgba(255,255,255,1) 100%);
}

body {
  color: rgb(72, 72, 72);
  display: block;
}

.s-l:after {
  content: '';
  position: relative;
  top: 0;
  left: 0;
  background: #EFEFEF;
  transform-origin: 60% 0;
  transform: skew(-60deg);
  z-index: -1;
}

.progress-bar {
  background-image: url(https://images.unsplash.com/photo-1557683316-973673baf926?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1000&q=80);
  background-attachment: fixed;
  background-size: 100%;
}

.icon {
  color: #6c757d;
}

.gitlab:hover {
  fill: rgba(252,109,39,1);
}

.github:hover {
  fill: rgba(0,0,0,1);
}

.twitter:hover {
  fill: #5da5d5;
}

.linkedin:hover {
  fill: #0f78b5;
}

@media (min-width: 600px) {
  .list-group-item:first-child {
    border-radius: 50px 0px 0px 50px!important;
    margin-left: auto;
  }

  .list-group-item:last-child {
    border-radius: 0px 50px 50px 0px!important;
    margin-right: auto;
  }
}

.php:hover {
  color: #5f7caf;
}

.react:hover {
  color: #23d9fd;
}


.list-group-item:hover {
  background-color: #efefef;
}
